  font-weight: 500;
  cursor: pointer;
}

span:hover {
  cursor: pointer;
}

input {
  outline: none;
}

.logform > input {
  width: 45vw;
  padding: 10px;
  margin: 2% auto;
  max-width: 500px;
  width: 66vw;
  font-size: 18px;
  border: none;
  outline: none;
  border-bottom: 1px solid black;
}

.logform > input:focus {
  /* border: 2px solid rgb(251, 190, 14); */
}

.formbutton {
  width: 25vw;
  height: 6vh;
  border-radius: 10px !important;
  color: white;
}

.header {
  background-color: #fff !important;
  padding: 0 3vw;
